import qs from 'qs'
import axios from 'axios'
import router from '@/router'
import config from './ajaxConfig'

const ajaxBase = axios.create({
  url: '',
  method: 'get',
  baseURL: config.baseURL, //
  params: {}, // 
  // bodyPUTPOSTPATCH
  //  `transformRequest` 
  // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
  // - FormData, File, Blob
  // - Node  Stream
  // data: {

  // },
  responseType: 'json',
  transformRequest: [function (data, headers) { // 
    return qs.stringify(data, {
      // arrayFormat: 'brackets'
    })
  }],
  headers: { // 
    'X-Requested-With': 'XMLHttpRequest'
  },

  paramsSerializer: function (params) { // 
    return qs.stringify(params, {
      arrayFormat: 'brackets'
    })
  },

  // timeout: 3000, // 
  withCredentials: false, // 

  auth: { // 

  },
  onUploadProgress: function (progressEvent) {

  },
  onDownloadProgress: function (progressEvent) {

  },
  validateStatus: function (status) { // trueundefinedresolvedrejected
    return status >= 200 && status < 300 // 
  }

})

// 
ajaxBase.interceptors.request.use(function (config) {
  return config
}, function (error) {
  return Promise.reject(error)
})

// 
ajaxBase.interceptors.response.use(function (response) {
  console.log(response)
  if (response.data instanceof Blob || +response.data.code === 200 || +response.data.code === 304) {
    return Promise.resolve(response.data)
  } else {
    if (+response.data.code === 1001 || +response.data.code === 1003) { // token
      router.push('/Login/back')
    }
    return Promise.reject(response.data)
  }
}, function (error) {
  console.log(error)
  if (error.response.status >= 400 && error.response.status < 404) {
    router.push('/Login/back')
  }
  return Promise.reject(error)
})

export default ajaxBase



